import React, { Component } from 'react';
import './App.css';
import { Switch, Route, withRouter } from 'react-router-dom';
import TestContainer1 from '../containers/TestContainer1';

//Pages component
import MainPage from './pages/MainPage';
import UslugiPage from './pages/UslugiPage';
import SpecialistsPage from './pages/SpecialistsPage';
import SecurityPage from './pages/SecurityPage';
import DocumentsPage from './pages/DocumentsPage';
import NoMatch from './pages/NoMatch';

class App extends Component {
  render() {
    return (
        <Switch>
          <Route path="/"  exact component={MainPage} />
          <Route path="/uslugi"  exact component={UslugiPage} />
          <Route path="/specialists"  exact component={SpecialistsPage} />
          <Route path="/security"  exact component={SecurityPage} />
          <Route path="/documents"  exact component={DocumentsPage} />
          <Route path="/test" exact component={TestContainer1} />
          <Route component={NoMatch} />
        </Switch>
    );
  }
}



export default withRouter(App)



