const API_URL = 'http://api.url/';

export const client = (variables, query) => 
    fetch(API_URL + query.urlParams, 
        {
            method: query.method,  
            headers: query.headers,  
            body: JSON.stringify(variables)
        })
        .then(res => {
            if(res.status !== 200) {
                throw Error(res._bodyText);
            } else {
                return res.json()
            }
        })
        .catch( err => {
            throw err
        })



